import React from "react"
import { Box, Container, Typography, TextField, Button } from "@mui/material"

const Contact = () => {
  return (
    <Box id="contact" sx={{ py: 8, backgroundColor: "#1a73e8", color: "#fff" }}>
      <Container>
        <Typography
          variant="h4"
          component="h2"
          sx={{ fontWeight: "bold", mb: 4, textAlign: "center" }}
        >
          Contact Me
        </Typography>
        <form
          action="https://formsubmit.co/your-email@example.com"
          method="POST"
          style={{ maxWidth: "600px", margin: "0 auto" }}
        >
          <TextField
            label="Name"
            name="name"
            fullWidth
            required
            margin="normal"
            variant="filled"
            sx={{ backgroundColor: "#fff", borderRadius: 1 }}
          />
          <TextField
            label="Email"
            name="email"
            type="email"
            fullWidth
            required
            margin="normal"
            variant="filled"
            sx={{ backgroundColor: "#fff", borderRadius: 1 }}
          />
          <TextField
            label="Message"
            name="message"
            fullWidth
            required
            multiline
            rows={4}
            margin="normal"
            variant="filled"
            sx={{ backgroundColor: "#fff", borderRadius: 1 }}
          />
          <Button
            type="submit"
            variant="contained"
            color="secondary"
            fullWidth
            sx={{ mt: 2 }}
          >
            Send Message
          </Button>
        </form>
      </Container>
    </Box>
  )
}

export default Contact
