import React from "react"
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  CardActions,
  Button,
} from "@mui/material"

const projects = [
  {
    title: "Project One",
    description:
      "A brief description of your project showcasing its features and purpose.",
    link: "https://github.com/your-repo/project-one",
    demo: "https://your-demo-link.com",
  },
  {
    title: "Project Two",
    description:
      "A short overview of another project highlighting its impact and technologies used.",
    link: "https://github.com/your-repo/project-two",
    demo: "https://your-demo-link.com",
  },
  // Add more projects here
]

const Projects = () => {
  return (
    <Box id="projects" sx={{ py: 8 }}>
      <Container>
        <Typography
          variant="h4"
          component="h2"
          sx={{ fontWeight: "bold", mb: 4, textAlign: "center" }}
        >
          Projects
        </Typography>
        <Grid container spacing={4}>
          {projects.map((project, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Card
                sx={{
                  height: "100%",
                  display: "flex",
                  flexDirection: "column",
                }}
              >
                <CardContent>
                  <Typography variant="h5" component="div" sx={{ mb: 2 }}>
                    {project.title}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    {project.description}
                  </Typography>
                </CardContent>
                <CardActions sx={{ mt: "auto" }}>
                  <Button
                    size="small"
                    href={project.link}
                    target="_blank"
                    rel="noopener"
                  >
                    GitHub
                  </Button>
                  <Button
                    size="small"
                    href={project.demo}
                    target="_blank"
                    rel="noopener"
                  >
                    Live Demo
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Box>
  )
}

export default Projects
